.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.header__logo-img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.header__nav {
  align-items: center;
  height: 60px;
  display: flex;
}

.header__nav-list {
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.header__nav-link.is-active {
  color: #00acdc;
}

.header__nav-link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.header__nav-item:hover > .header__nav-link {
  color: #00acdc;
}

.header__nav-item:not(:last-of-type) {
  margin-right: 20px;
  position: relative;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 408px;
  padding-top: 32px;
  transition-duration: .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  display: block;
}

.card__title-wrapper {
  text-align: left;
  margin-top: 40px;
}

.card__title {
  color: #060b35;
  margin: 0 16px 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: #616070;
  text-align: left;
  margin: 4px 16px 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__buybtn {
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: calc(100% - 34px);
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buybtn:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  gap: 4px;
  width: 100%;
  padding: 0 16px;
  display: flex;
}

.card__rating .stars {
  gap: 4px;
  display: flex;
}

.card__rating .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.card__rating .stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__rating .card__reviews {
  color: #060b35;
  margin-top: 2px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.price {
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 32px);
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.price__title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.price__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}
